<template>
    <div class="fox-form-item">
        <label
            v-if="label"
            class="fox-form-item-label"
            :class="[`fox-form-item-label-${form.labelPosition}`]"
            :style="{ width: getWidth }"
            >{{ label }}</label
        >
        <div class="fox-form-item-content" :style="{ marginLeft: getWidth }">
            <slot></slot>
            <div v-if="validateState === 'error'" class="fox-form-item-message">{{ validateMessage }}</div>
        </div>
    </div>
</template>

<script>
import { prefix } from "@/utils/assist.js";
import Emitter from "@/mixins/emitter.js";
import AsyncValidator from "async-validator";
export default {
    name: `${prefix}FormItem`,
    mixins: [Emitter],
    inject: ["form"],
    props: {
        label: {
            type: String,
            default: "",
        },
        prop: String,
        labelWidth: [String, Number],
    },
    data() {
        return {
            isRequired: false, // 
            validateState: "", // 
            validateMessage: "", // 
        };
    },
    //  Form 
    mounted() {
        //  prop
        if (this.prop) {
            this.dispatch("foxForm", "on-form-item-add", this);
            // 
            this.initialValue = this.fieldValue;
            this.setRules();
        }
    },
    methods: {
        // 
        resetField() {
            this.validateState = "";
            this.validateMessage = "";

            this.form.model[this.prop] = this.initialValue;
        },
        //  Form  rules  FormItem 
        getRules() {
            let formRules = this.form.rules;

            formRules = formRules ? formRules[this.prop] : [];

            return [].concat(formRules || []);
        },
        //  blur  change rule 
        getFilteredRule(trigger) {
            const rules = this.getRules();
            return rules.filter(rule => !rule.trigger || rule.trigger.indexOf(trigger) !== -1);
        },
        /**
         * 
         * @param trigger 
         * @param callback 
         */
        validate(trigger, callback = function() {}) {
            let rules = this.getFilteredRule(trigger);

            if (!rules || rules.length === 0) {
                return true;
            }

            // 
            this.validateState = "validating";

            //  async-validator 
            let descriptor = {};
            descriptor[this.prop] = rules;

            const validator = new AsyncValidator(descriptor);
            let model = {};

            model[this.prop] = this.fieldValue;

            validator.validate(model, { firstFields: true }, errors => {
                this.validateState = !errors ? "success" : "error";
                this.validateMessage = errors ? errors[0].message : "";

                callback(this.validateMessage);
            });
        },
        onFieldBlur() {
            this.validate("blur");
        },
        onFieldChange() {
            this.validate("change");
        },
        setRules() {
            let rules = this.getRules();
            if (rules.length) {
                rules.every(rule => {
                    // 
                    this.isRequired = rule.required;
                });
            }
            this.$on("on-form-blur", this.onFieldBlur);
            this.$on("on-form-change", this.onFieldChange);
        },
    },
    computed: {
        //  Form  model 
        fieldValue() {
            return this.form.model[this.prop];
        },
        getWidth() {
            const width = this.labelWidth || this.form.labelWidth || false;
            return width ? width + "px" : "auto";
        },
    },
    //  Form 
    beforeDestroy() {
        this.dispatch("foxForm", "on-form-item-remove", this);
    },
};
</script>

<style lang="scss">
@import "@/style/form-item.scss";
</style>
